"use client";

import * as React from "react";
import dynamic from "next/dynamic";
import { DashboardLayout } from "@/components/dashboard/layout";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Switch } from "@/components/ui/switch";
import { Button } from "@/components/ui/button";
import { Label } from "@/components/ui/label";
import {
  GripVertical,
  Loader2,
  Trash2,
} from "lucide-react";
import { useParams } from "next/navigation";
import axiosClient from "@/lib/axiosClient";

import {
  DndContext,
  closestCenter,
  PointerSensor,
  useSensor,
  useSensors,
} from "@dnd-kit/core";

import {
  arrayMove,
  SortableContext,
  verticalListSortingStrategy,
  useSortable,
} from "@dnd-kit/sortable";

import { CSS } from "@dnd-kit/utilities";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
  SelectGroup,
  SelectLabel,
} from "@/components/ui/select";
import {
  Accordion,
  AccordionContent,
  AccordionItem,
  AccordionTrigger,
} from "@/components/ui/accordion";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { useToast } from "@/components/ui/use-toast"; // Assuming you have shadcn toast

const Perview = dynamic(() => import("@/app/preview/page"), { ssr: false });

type GlobalSection = {
  key: string;
  name: string;
  schema: any;
  group?: string;
  sub_group?: string | null;
};

type ActiveSection = {
  sectionId?: number; // product_landing_page_sections.id
  sectionKey: string;
  label: string;
  enabled: boolean;
  content: Record<string, any>;
  schema: any;
  saving?: boolean;
};

export default function LandingPageBuilder() {
  const { toast } = useToast();
  const params = useParams();
  const productId = params?.productId ? Number(params.productId) : 32;

  const [isLoading, setIsLoading] = React.useState(true);
  const [globalSections, setGlobalSections] = React.useState<GlobalSection[]>([]);
  const [groupedGlobals, setGroupedGlobals] = React.useState<Record<string, Record<string, GlobalSection[]>>>({});
  const [activeSections, setActiveSections] = React.useState<ActiveSection[]>([]);
  const [reordering, setReordering] = React.useState(false);

  const sensors = useSensors(useSensor(PointerSensor, { activationConstraint: { distance: 8 } }));

  /* ----------------------------------------------------
     FETCH DATA
  ---------------------------------------------------- */
  React.useEffect(() => {
    const fetchData = async () => {
      try {
        setIsLoading(true);

        // Fetch global sections (always)
        const globalRes = await axiosClient.get("/landing-page/sections");
        console.log("Global Sections Response:", globalRes);
        if (globalRes.data.success) {
          const sections = globalRes.data.data.sections.filter((s: any) => s.is_active);
          setGlobalSections(sections);
          setGroupedGlobals(globalRes.data.data.grouped || {});
        }

        let active: ActiveSection[] = [];

        if (productId) {
          // Fetch product-specific configuration
          const productRes = await axiosClient.get(`/products/${productId}/landing-page`);
          if (productRes.data.success && productRes.data.data?.sections?.length > 0) {
            active = productRes.data.data.sections
              .sort((a: any, b: any) => a.sort_order - b.sort_order)
              .map((sec: any) => ({
                sectionId: sec.id,
                sectionKey: sec.global_section.key,
                label: sec.global_section.name,
                enabled: sec.is_published,
                content: sec.content || {},
                schema: sec.global_section.schema,
              }));
          }
        }
        // If no product or no sections yet, start empty
        setActiveSections(active);
      } catch (error) {
        toast({ title: "Error", description: "Failed to load data", variant: "destructive" });
      } finally {
        setIsLoading(false);
      }
    };

    fetchData();
  }, [productId, toast]);

  /* ----------------------------------------------------
     ADD SECTION
  ---------------------------------------------------- */
  const handleAddSection = async (sectionKey: string) => {
    if (!productId || !sectionKey) return;
    if (activeSections.some(s => s.sectionKey === sectionKey)) return;

    try {
      const res = await axiosClient.post(`/products/${productId}/landing-page/sections`, {
        global_section_key: sectionKey,
      });

      if (res.data.success) {
        const newSec = res.data.data;
        setActiveSections(prev => [...prev, {
          sectionId: newSec.id,
          sectionKey: newSec.global_section.key,
          label: newSec.global_section.name,
          enabled: newSec.is_published,
          content: newSec.content || {},
          schema: newSec.global_section.schema,
        }]);
        toast({ title: "Section added" });
      }
    } catch (error) {
      toast({ title: "Error", description: "Failed to add section", variant: "destructive" });
    }
  };

  /* ----------------------------------------------------
     TOGGLE PUBLISH
  ---------------------------------------------------- */
  const togglePublish = async (sec: ActiveSection) => {
    if (!productId || !sec.sectionId) return;

    const newEnabled = !sec.enabled;
    setActiveSections(prev => prev.map(s => s.sectionId === sec.sectionId ? { ...s, enabled: newEnabled } : s));

    try {
      await axiosClient.patch(`/products/${productId}/landing-page/sections/${sec.sectionId}/publish`, {
        is_published: newEnabled,
      });
    } catch (error) {
      // rollback
      setActiveSections(prev => prev.map(s => s.sectionId === sec.sectionId ? { ...s, enabled: !newEnabled } : s));
      toast({ title: "Error", description: "Failed to update publish status", variant: "destructive" });
    }
  };

  /* ----------------------------------------------------
     REMOVE SECTION
  ---------------------------------------------------- */
  const removeSection = async (sec: ActiveSection) => {
    if (!productId || !sec.sectionId) return;

    setActiveSections(prev => prev.filter(s => s.sectionId !== sec.sectionId));

    try {
      await axiosClient.delete(`/products/${productId}/landing-page/sections/${sec.sectionId}`);
    } catch (error) {
      toast({ title: "Error", description: "Failed to remove section", variant: "destructive" });
      // Optionally refetch
    }
  };

  /* ----------------------------------------------------
     CONTENT UPDATE (debounced auto-save)
  ---------------------------------------------------- */
  const updateContent = React.useCallback(
    async (sec: ActiveSection, newContent: Record<string, any>) => {
      if (!productId || !sec.sectionId) return;

      setActiveSections(prev =>
        prev.map(s => s.sectionId === sec.sectionId ? { ...s, content: newContent, saving: true } : s)
      );

      try {
        await axiosClient.put(`/products/${productId}/landing-page/sections/${sec.sectionId}`, {
          content: newContent,
        });
        setActiveSections(prev =>
          prev.map(s => s.sectionId === sec.sectionId ? { ...s, saving: false } : s)
        );
      } catch (error) {
        toast({ title: "Error", description: "Failed to save content", variant: "destructive" });
        setActiveSections(prev =>
          prev.map(s => s.sectionId === sec.sectionId ? { ...s, saving: false } : s)
        );
      }
    },
    [productId, toast]
  );

  const debouncedUpdate = React.useMemo(() => {
    let timeout: NodeJS.Timeout;
    return (sec: ActiveSection, content: Record<string, any>) => {
      clearTimeout(timeout);
      timeout = setTimeout(() => updateContent(sec, content), 800);
      // Optimistic local update
      setActiveSections(prev => prev.map(s => s.sectionId === sec.sectionId ? { ...s, content } : s));
    };
  }, [updateContent]);

  /* ----------------------------------------------------
     REORDER
  ---------------------------------------------------- */
  const onDragEnd = async ({ active, over }: any) => {
    if (!over || active.id === over.id || !productId) return;

    const oldIndex = activeSections.findIndex(s => s.sectionId === active.id);
    const newIndex = activeSections.findIndex(s => s.sectionId === over.id);

    const newOrder = arrayMove(activeSections, oldIndex, newIndex);

    setActiveSections(newOrder);
    setReordering(true);

    try {
      await axiosClient.post(`/products/${productId}/landing-page/reorder`, {
        sections: newOrder.map((sec, idx) => ({
          section_id: sec.sectionId,
          sort_order: idx + 1,
        })),
      });
    } catch (error) {
      toast({ title: "Error", description: "Failed to reorder", variant: "destructive" });
      // rollback
      setActiveSections(activeSections);
    } finally {
      setReordering(false);
    }
  };

  /* ----------------------------------------------------
     PREVIEW PAYLOAD
  ---------------------------------------------------- */
  const previewPayload = {
    templateName: "modern",
    sections: activeSections
      .filter(s => s.enabled)
      .map(s => ({ section: s.sectionKey, content: s.content })),
    styles: {
      primaryColor: "#ecedf4ff",
      backgroundColor: "#000000",
      textColor: "#a97d38",
      headlineSize: 28,
      paragraphSize: 16,
    },
  };

  const pageTitle = productId
    ? `Product #${productId} Landing Page Builder`
    : "Global Landing Page Builder";

  if (isLoading) {
    return (
      <DashboardLayout>
        <div className="flex items-center justify-center h-screen">
          <Loader2 className="h-12 w-12 animate-spin" />
          <p className="ml-4 text-lg">Loading...</p>
        </div>
      </DashboardLayout>
    );
  }

  return (
    <DashboardLayout>
      <div className="max-w-7xl mx-auto space-y-8 p-6">
        <h1 className="text-3xl font-bold">{pageTitle}</h1>

        <div className="grid lg:grid-cols-2 gap-8">
          {/* Controls */}
          <div className="space-y-6">
            {productId && (
              <Card>
                <CardHeader>
                  <CardTitle>Add Section</CardTitle>
                </CardHeader>
                <CardContent>
                  <Select onValueChange={handleAddSection}>
                    <SelectTrigger>
                      <SelectValue placeholder="Choose a section to add..." />
                    </SelectTrigger>
                    <SelectContent>
                      {Object.entries(groupedGlobals).map(([group, subGroups]) => (
                        <SelectGroup key={group}>
                          <SelectLabel>{group.charAt(0).toUpperCase() + group.slice(1)}</SelectLabel>
                          {Object.entries(subGroups).map(([subGroup, secs]) => (
                            subGroup === "null" ? (
                              secs
                                .filter(s => !activeSections.some(a => a.sectionKey === s.key))
                                .map(s => (
                                  <SelectItem key={s.key} value={s.key}>
                                    {s.name}
                                  </SelectItem>
                                ))
                            ) : (
                              <SelectGroup key={subGroup}>
                                <SelectLabel className="pl-8">
                                  {subGroup ? subGroup : "General"}
                                </SelectLabel>
                                {secs
                                  .filter(s => !activeSections.some(a => a.sectionKey === s.key))
                                  .map(s => (
                                    <SelectItem key={s.key} value={s.key}>
                                      {s.name}
                                    </SelectItem>
                                  ))}
                              </SelectGroup>
                            )
                          ))}
                        </SelectGroup>
                      ))}
                      {activeSections.length === globalSections.length && (
                        <p className="p-4 text-center text-muted-foreground">All sections added</p>
                      )}
                    </SelectContent>
                  </Select>
                </CardContent>
              </Card>
            )}

            <Card>
              <CardHeader>
                <CardTitle>Sections</CardTitle>
                <CardDescription>Drag to reorder  Edit content below</CardDescription>
              </CardHeader>
              <CardContent>
                {activeSections.length > 0 ? (
                  <DndContext sensors={sensors} collisionDetection={closestCenter} onDragEnd={onDragEnd}>
                    <SortableContext items={activeSections.map(s => s.sectionId!)} strategy={verticalListSortingStrategy}>
                      <Accordion type="multiple" className="space-y-4">
                        {activeSections.map((item) => (
                          <SortableSectionItem
                            key={item.sectionId}
                            item={item}
                            onTogglePublish={() => togglePublish(item)}
                            onRemove={() => removeSection(item)}
                            onContentChange={(newContent) => debouncedUpdate(item, newContent)}
                            disabled={reordering}
                          />
                        ))}
                      </Accordion>
                    </SortableContext>
                  </DndContext>
                ) : (
                  <p className="text-center py-8 text-muted-foreground">
                    No sections yet. Add one above.
                  </p>
                )}
              </CardContent>
            </Card>
          </div>

          {/* Preview */}
          <Card className="sticky top-6">
            <CardHeader>
              <CardTitle>Mobile Preview</CardTitle>
            </CardHeader>
            <CardContent className="flex justify-center py-8">
              <div className="w-[360px] h-[720px] rounded-[45px] border-[14px] border-black bg-black overflow-hidden relative shadow-2xl">
                <div className="absolute top-0 left-1/2 -translate-x-1/2 w-40 h-6 bg-black rounded-b-3xl z-20" />
                <div className="bg-white w-full h-full overflow-y-auto">
                  <Perview data={previewPayload} />
                </div>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </DashboardLayout>
  );
}

/* ----------------------------------------------------
   SORTABLE SECTION ITEM
---------------------------------------------------- */
function SortableSectionItem({
  item,
  onTogglePublish,
  onRemove,
  onContentChange,
  disabled,
}: {
  item: ActiveSection;
  onTogglePublish: () => void;
  onRemove: () => void;
  onContentChange: (content: Record<string, any>) => void;
  disabled?: boolean;
}) {
  const {
    attributes,
    listeners,
    setNodeRef,
    transform,
    transition,
    isDragging,
  } = useSortable({ id: item.sectionId!, disabled });

  const style = {
    transform: CSS.Transform.toString(transform),
    transition,
    opacity: isDragging ? 0.5 : 1,
  };

  const handleFieldChange = (fieldKey: string, value: any) => {
    onContentChange({ ...item.content, [fieldKey]: value });
  };

  return (
    <AccordionItem value={item.sectionKey} className="border rounded-lg shadow-sm" ref={setNodeRef} style={style}>
      <AccordionTrigger className="px-4 hover:no-underline">
        <div className="flex items-center justify-between w-full">
          <div className="flex items-center gap-3">
            <GripVertical
              className="h-5 w-5 text-gray-400 cursor-grab"
              {...attributes}
              {...listeners}
            />
            <span className="font-medium">{item.label}</span>
            {item.saving && <Loader2 className="h-4 w-4 animate-spin ml-2" />}
          </div>
          <div className="flex items-center gap-4 pr-4">
            <Switch checked={item.enabled} onCheckedChange={onTogglePublish} />
            <Button variant="ghost" size="icon" onClick={(e) => { e.stopPropagation(); onRemove(); }}>
              <Trash2 className="h-4 w-4 text-red-600" />
            </Button>
          </div>
        </div>
      </AccordionTrigger>
      <AccordionContent className="px-6 pb-6 space-y-4">
        {item.schema?.fields?.length > 0 ? (
          item.schema.fields.map((field: any) => (
            <div key={field.key} className="space-y-2">
              <Label htmlFor={field.key}>
                {field.label} {field.required && <span className="text-red-500">*</span>}
              </Label>

              {field.type === "text" && (
                <Input
                  id={field.key}
                  value={item.content[field.key] || ""}
                  onChange={(e) => handleFieldChange(field.key, e.target.value)}
                  placeholder={field.placeholder || field.label}
                />
              )}

              {field.type === "textarea" && (
                <Textarea
                  id={field.key}
                  value={item.content[field.key] || ""}
                  onChange={(e) => handleFieldChange(field.key, e.target.value)}
                  placeholder={field.placeholder || field.label}
                  rows={5}
                />
              )}

              {field.type === "image" && (
                <div className="space-y-2">
                  <Input
                    id={field.key}
                    value={item.content[field.key] || ""}
                    onChange={(e) => handleFieldChange(field.key, e.target.value)}
                    placeholder="https://example.com/image.jpg"
                  />
                  {item.content[field.key] && (
                    <img
                      src={item.content[field.key]}
                      alt="Preview"
                      className="w-full max-h-96 object-contain rounded-lg border"
                      onError={(e) => (e.currentTarget.src = "/placeholder.svg")}
                    />
                  )}
                </div>
              )}

              {/* Add more field types as needed */}
            </div>
          ))
        ) : (
          <p className="text-sm text-muted-foreground">No editable fields.</p>
        )}
      </AccordionContent>
    </AccordionItem>
  );
}